import { graphql, useStaticQuery } from 'gatsby';
import React from 'react';
import styled from 'styled-components';
import Divider from '../common/divider';

const Wrapper = styled.div`
    background: rgba(0, 0, 0, 0.75);
    height: calc(100vh - 100px);

    .bgImage {
        position: absolute;
        z-index: -1;
        width: 100%;
        height: calc(100vh - 100px);
        object-fit: cover;
    }
`;

const Background = styled.div`
    position: absolute;
    width: 100%;
    height: calc(100vh - 100px);
`;

const Content = styled.div`
    position: absolute;
    z-index: 10;
    color: white;
    width: 100%;
    height: 100%;

    margin-top: -100px;

    display: flex;
    justify-content: center;
    align-items: center;


    h1 {
        font-size: 92px;

        @media screen and (max-width: 576px) {
            font-size: 60px;
        }
    }
`;

const Hero = () => {

    const data = useStaticQuery(query);

    const bgImage = data.bgImage.childImageSharp.fluid.src;

    return (
        <>
            <Wrapper>
                <img src={bgImage} className="bgImage" />
                <Background className="content">
                    <Content>
                        <div className="text-center">
                            <h1 className="text-gold">Арно Бистро</h1>
                            <Divider addClassName="gold" />

                            <p>Традиционна българска кухня в центъра на София</p>
                        </div>
                    </Content>
                </Background>
            </Wrapper>
        </>
    )

}

const query = graphql`
    query IndexHero {
        bgImage: file(relativePath: {eq: "index/hero.jpg"}) {
            childImageSharp {
                fluid(quality: 100) {
                    src
                }
            }
        }
    }
`;


export default Hero;